import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponentComponent } from './home/home-component/home-component.component';
import { ListaComponentComponent } from './lista/lista-component/lista-component.component';
import { LoginComponentComponent } from './login/login-component/login-component.component';
import {RepuestaComponent} from './respuesta/repuesta/repuesta.component'



const routes: Routes = [
  {path:'login', component: LoginComponentComponent},
  {path:'home', component: HomeComponentComponent},
  {path:'list', component: ListaComponentComponent},
  {path:'respuesta', component: RepuestaComponent},
  {path:'**', pathMatch: 'full', redirectTo:'login'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
