import { Component, OnInit } from '@angular/core';
import { Pokemon } from 'src/app/modelo/pokemon';
import { ServicioService } from 'src/app/servicios/servicio.service';

@Component({
  selector: 'app-repuesta',
  templateUrl: './repuesta.component.html',
  styleUrls: ['./repuesta.component.css']
})
export class RepuestaComponent implements OnInit {

  noEncontrado = false;
  pokemon: Pokemon;

  pokemones: any;

  constructor(private servicioService: ServicioService) {this.pokemon = {
    name: '',
    url:  '',
  } 
  this.obtenerPokemon()
}

  ngOnInit(): void {
  }

  obtenerPokemon(){
    this.noEncontrado = false;
    this.pokemon = {
      name: '',
      url:  '',
    };
    this.servicioService.obtenerPokemon().subscribe(
      (pokemon:any) => {
        
        console.log(pokemon + 'hola Mundo')
        
        this.pokemon = pokemon
        this.pokemones = pokemon.results
      },(err: any) => {console.log(err); this.noEncontrado =true}
    )
  }


  onclick(){
    console.log(this.pokemon);
  }
}
