import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Pokemon } from '../modelo/pokemon';



@Injectable({
  providedIn: 'root'
})
export class ServicioService {
  baseURL = environment.webapiURL + 'pokemon?limit=100&offset=200';

  usuarios: any;
  constructor(private http: HttpClient) {

    this.usuarios = [{
      nombre: 'Whanderley',
      apellido: 'Fonseca',
      edad: 23,
      genero: 'M'
    },{
      nombre: 'Kenneth',
      apellido: 'Sanabria',
      edad: 22,
      genero: 'M'
    },{
      nombre: 'Jose Luis',
      apellido: 'Botoneta',
      edad: 0.8,
      genero: 'M'
    },{
      nombre: 'Aguacate',
      apellido: 'Mauricio',
      edad: 1,
      genero: 'M'
    },{
      nombre: 'Senorshh',
      apellido: 'Sanabria',
      edad: 30,
      genero: 'F'
    }]

  }

  obtenerPokemon():Observable<Pokemon>{
    const url = this.baseURL;
    return this.http.get<Pokemon>(url);
  }

}
